import Vue from 'vue'
import VueRouter from 'vue-router'
//安装插件
Vue.use(VueRouter)
// 2.创建路由对象
// import DetailPage from "@/DetailsPage/DetailPage";
import HeaderWrapper from "@/components/HeaderWrapper";

const router = new VueRouter({
    mode: 'history',
    routes:[
        {
            path:'/foo',
            component:HeaderWrapper
        },
        {
            path: '/bar',
            component: HeaderWrapper
        }
    ]
});
//导出
export default router