<template>
    <div>
        <div class="hover" @click="doThis"
             :style="{'left':left+'px','top':top+'px'}" :draggable="true" ref="div">
                <i class="el-icon-star-off"></i>
            微信联系
        </div>
            <img src="https://ss0.bdstatic.com/94oJfD_bAAcT8t7mm9GUKT-xh_/timg?image&quality=100&size=b4000_4000&sec=1588957877&di=763388f776cd37a0609ba2220632a9d4&src=http://cms.an.m.liebao.cn/images/logos/201704/523_1493015466.jpg" alt=""
                 class="code" v-show="visibility" :style="{'left':left-125+'px','top':top-200+'px'}">
            <a target="_blank" href="http://wpa.qq.com/msgrd?v=3&uin=2890276601&site=qq&menu=yes" class="Jump"
               :style="{'left':left+'px','top':top+90+'px'}">
                <img border="0" src="http://wpa.qq.com/pa?p=2:2890276601:51" alt="点击这里给我发消息" title="点击这里给我发消息"/>
            </a>
    </div>
</template>

<script>
    export default {
        name: "HoverButton",
        // 初始化按钮的位置
        created(){
            this.left = document.documentElement.clientWidth-75;
            this.top = document.documentElement.clientHeight*0.75;
        },
        //位置信息
        data(){
            return{
                left:0,
                top:0,
                timer:null,
                currentTop:0,
                //图片显示
                visibility:false
            }
        },
        // 监听滚动事件
        mounted(){
            window.addEventListener('scroll', this.handleScrollStart);
            // console.log('在挂载后被调用，也不能在服务器端渲染时被调用；这个函数里，是可以获取元素，并进行操作的');
            this.$nextTick(()=>{
                const div = this.$refs.div;
                div.addEventListener("touchstart",()=>{
                    div.style.transition = 'none';
                });
                div.addEventListener("touchmove",(e)=>{
                    if (e.targetTouches.length === 1) {//一根手指
                        let touch = event.targetTouches[0];
                        this.left = touch.clientX;
                        this.top = touch.clientY;
                    }

                });
                div.addEventListener("touchend",()=>{
                    if(this.left>document.documentElement.clientWidth/2){
                        this.left = document.documentElement.clientWidth - 75;
                    }else{
                        this.left = 0;
                    }

                });
            });
        },
        //滚动和停止函数
        methods:{
            handleScrollStart(){
                this.timer&&clearTimeout(this.timer);
                this.timer = setTimeout(()=>{
                    this.handleScrollEnd();
                },300);
                this.currentTop = document.documentElement.scrollTop || document.body.scrollTop;
                if (this.visibility){
                    this.visibility=!this.visibility;
                }
                this.left = document.documentElement.clientWidth;
            },
            handleScrollEnd(){
                let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
                // console.log(scrollTop);
                if(scrollTop === this.currentTop){
                    this.left = document.documentElement.clientWidth - 75;
                    clearTimeout(this.timer);
                }
            },
            doThis(){
                this.visibility=!this.visibility;
            }

        },
        //结束移除
        beforeDestroy(){
            window.removeEventListener('scroll', this.handleScrollStart);
        },

    }
</script>

<style lang="scss" scoped>
    .hover{
        position: fixed;
        z-index: 999;
        transition: all 0.1s;
        .el-icon-star-off{
            font-size:60px
        }
    }
    .code{
        width: 200px;
        height: 200px;
        position: fixed;
        z-index: 1000;
    }
    .Jump{
        z-index: 1000;
        position: fixed;
    }
</style>